import Navbar from './Navbar';
import BoardList from './BoardList';
import React, { useContext, useEffect } from 'react';
import Quiver from './Quiver';
import Dialog from "@material-ui/core/Dialog";
import {QuiverContext} from './context.quiver';
import CustomForm from './CustomForm';


export default function SgrApp() {
  
const { dialogOn, toggleDialog, quiverOn, quiver, initiateQuiver, setQuiver } = useContext(QuiverContext);


useEffect (() => {
   window.localStorage.setItem('quiver',JSON.stringify(quiver)) ;
});



  return (
    <div>
      <Navbar/>
    
       {!quiverOn ? <BoardList/> : <Quiver/>}
      <Dialog
          open={dialogOn}
          onClose={toggleDialog}
          aria-labelledby="responsive-dialog-title"
        >
          <CustomForm /> 
        </Dialog>
    </div>
  )
}
